import { Grid, Typography } from "@mui/material";
import React from "react";
import theme from "../theme";
import ContentWrapper from "./ContentWrapper";
import PhotosShower from "./PhotosShower";

const photosInterier = [
    "place1.jpg",
    "place2.jpg",
    "fireplace.jpg",
    "fireplace1.jpg",
    "hall.jpg",
    "mainhall.jpg",
    "mainhall1.jpg",
    "sofa.jpg",
];
const photosKaraoke = ["/images/photos/karaoke.jpg", "/images/photos/karaoke1.jpg", "/images/photos/karaoke2.jpg",];

const Interier = () => {

    return (
        <ContentWrapper bgColor="white" id="interier" color="black">
            <Grid container sx={{ textAlign: "left" }} spacing={{ xs: 2, sm: 6 }}>
                <Grid xs={12} sm={6} item>
                    <PhotosShower photoArr={photosInterier.map((url) => "/images/photos/" + url)} />
                </Grid>
                <Grid xs={12} sm={6} item order={{ xs: -1, sm: 0 }}>
                    <Typography variant="h3" sx={{ mb: "16px", color: theme.palette.pink.main }}></Typography>
                    <Typography> 3 :</Typography>
                    <Typography sx={{ mb: "16px", color: theme.palette.background.default, display: "inline" }} fontWeight={"bold"}>  </Typography>
                    <Typography sx={{ display: "inline" }}>-       </Typography>
                    <br /><br />
                    <Typography sx={{ mb: "16px", color: theme.palette.pink.main, display: "inline" }} fontWeight={"bold"}> </Typography>
                    <Typography sx={{ display: "inline" }}> -  ,      </Typography>
                    <br /><br />
                    <Typography sx={{ color: "purple", display: "inline" }} fontWeight={"bold"}>2  </Typography>
                    <Typography sx={{ display: "inline" }}> -      (  )</Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography variant="h3" sx={{ mb: "16px", color: theme.palette.pink.main }}></Typography>
                    <Typography>
                              - AST 250.    70 000 . <br /><br />   ...
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <PhotosShower photoArr={photosKaraoke} />
                </Grid>
            </Grid>
        </ContentWrapper>
    )
}

export default Interier;